<template>

  <div>
    <ul class="clearfix">
        <li v-for="(photo,index) in photoList" :key="index">
          <router-link :to="'/photodetail/'+index">
          <img :src="photo.src" alt="">
          </router-link>
        </li>
    </ul>
  </div>
</template>


<script>
import axios from 'axios';
export default{
  data(){
    return{
      photoList:[],
      nowindex:0,
    }
  },
  created(){
    axios.get('/static/data/photodata.json').then(res=>{
      this.photoList=res.data.photoData;
      this.$store.dispatch('setPhotoListAction',res.data.photoData);
    })
  }
}
</script>

<style scoped>
  .photodata{
  }
  .photodata li{
    width: 49%;
    float: left;
    display: block;
    border:1px solid #fff;
  }
  .clearfix::after{
    content: '';
    display: block;
    clear: both;
  }

</style>






