import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);

const state={
  photoList:[]
};
const mutations={
setphotoList(state,status){
  state.photoList=status;
}
};
const actions={
  setPhotoListAction({commit},status){
    commit('setphotoList',status)
  }
};
const getters={

};
export default new Vuex.Store( {
  state,
  mutations,
  actions,
  getters
})





