  <v-touch @swipeleft="onSwipeLeft" @swiperight="onSwipeRight" @tap="tap" class="container" :style="styleObj">
  </v-touch>
  <!--<div class="container" :style="styleobj">-->
</template>

<script>
  import axios from 'axios';
  export default{
      data(){
          return{
              nowIndex:0
          }
      },
    computed:{
          styleObj(){
              return `background:#000 url(${this.$store.state.photoList[this.nowIndex].src}) no-repeat center/contain`
          }
    },
    created(){
          this.nowIndex=this.$route.params.index;
    },

    methods:{
        onSwipeLeft(){
            this.nowIndex--;
        },
      onSwipeRight(){
           this.nowIndex++ ;
      },
      tap(){
        this.$router.go(-1);
      }
    }
  }

</script>

<style>
  .container{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    /*background: #000;*/
  }

</style>




